.profile {
  width: 400px;
  margin: 40px auto;
  border-radius: 8px;
  overflow: hidden;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.avatar {
  width: 40%;
  margin: 50px;
  border-radius: 50%;
  border: 1px solid rgb(157, 160, 159);
}

.name {
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 24px;
  line-height: 26px;
}

.tag {
  margin-bottom: 30px;
  font-weight: 500;
  font-size: 20px;
  line-height: 22px;
}

.location {
  margin-bottom: 40px;
  font-weight: 500;
  font-size: 20px;
  line-height: 22px;
}
